    flex-direction: column;
    align-items: flex-start;
  }

  .topnav #nav-items {
    position: absolute;
    right: 0px;
    padding-top: 3vh;
    height: 100vh;
    background-color: ghostwhite;
    flex-direction: column;
    width: 65% !important;
    align-items: right;
    transform: translateX(100%);
    transition: transform 0.5s ease-in;
    display: none; 
    overflow-x: hidden;
    z-index: 2;
  }

  .topnav #nav-items #not-apply {
    width: 100%;
    font-family: 'Karla';
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
  } 

  .topnav #nav-items .apply{
    display: none;
  } 

  #nav-items.active{
    display: flex;
    width: 50%;
    flex-direction: column;
    align-items: right;
    font-size: 150%;
    display: flex;
    transform: translateX(0%);
  }
}

